// src/components/Projects.js
import React, { useState } from "react";
import { motion } from "framer-motion";
import Modal from "react-modal";
import { FaExternalLinkAlt } from "react-icons/fa"; // 导入外部链接图标

Modal.setAppElement("#root"); // Accessibility feature

const Projects = () => {
  const [modalIsOpen, setIsOpen] = useState(false);
  const [selectedProject, setSelectedProject] = useState(null);

  const projects = [
    {
      category: "Web Development Projects",
      items: [
        {
          title: "Financial Management Tool",
          tech: ["React", "Firebase", "Recharts"],
          description:
            "A tool to manage budgets and visualize data using Firebase and Recharts.",
          link: "https://personal-finance-app-amber.vercel.app/",
          details: "Detailed information about Financial Management Tool...",
        },
        {
          title: "Trip Map Tracker",
          tech: ["Next.js", "Geocoding API", "Leaflet"],
          description:
            "Mark locations, add trip details, and store logs with Leaflet and Geocoding API.",
          link: "https://travel-log-app-topaz.vercel.app/",
          details: "Detailed information about Trip Map Tracker...",
        },
        {
          title: "VisualDev UI",
          tech: ["React", "Tailwind CSS"],
          description:
            "A UI library with components and templates for marketing websites using React and Tailwind CSS.",
          link: "https://visualdev-ui.vercel.app/",
          details: "Detailed information about VisualDev UI...",
        },
      ],
    },
    {
      category: "Python Projects",
      items: [
        {
          title: "Handwritten Digit Recognition",
          tech: ["Python", "TensorFlow", "Keras"],
          description:
            "Real-time handwritten digit recognition with TensorFlow, Keras, and a Tkinter-based UI. This project focuses on interactive learning and user engagement.",
          link: "#",
          details:
            "Detailed information about Handwritten Digit Recognition...",
        },
        {
          title: "TED Persuasive Adjective Explorer",
          tech: ["Python", "SpaCy", "NLTK"],
          description:
            "Analyzed TED talks to identify persuasive adjectives using SpaCy and NLTK. This project uncovers language trends in different fields.",
          link: "#",
          details:
            "Detailed information about TED Persuasive Adjective Explorer...",
        },
      ],
    },
    {
      category: "Mobile Application Projects",
      items: [
        {
          title: "Travel Assistant",
          tech: ["React Native", "Firebase"],
          description:
            "A cross-platform app for managing trips, tracking expenses, and creating travel journals. Features include a packing list manager, real-time expense tracker, and photo uploads with comments.",
          link: "#",
          images: ["/RN1.PNG", "/RN2.PNG", "/RN3.PNG", "/RN4.PNG"],
          details: "Detailed information about Travel Assistant...",
        },
      ],
    },
  ];

  const openModal = (project) => {
    setSelectedProject(project);
    setIsOpen(true);
  };

  const closeModal = () => {
    setIsOpen(false);
    setSelectedProject(null);
  };

  return (
    <section
      id="projects"
      className="bg-gray-900 text-gray-100 rounded-3xl shadow-2xl p-10 mx-auto mt-12 w-11/12 max-w-7xl relative z-10"
      data-aos="fade-up"
    >
      <h2 className="text-4xl font-extrabold text-center mb-8">Projects</h2>
      {/* Projects */}
      {projects.map((projectGroup) => (
        <div key={projectGroup.category} className="mb-10">
          <h3 className="text-2xl font-bold text-purple-300 mb-6 text-center">
            {projectGroup.category}
          </h3>
          <div
            className={
              projectGroup.category === "Mobile Application Projects"
                ? "grid grid-cols-1 gap-6"
                : projectGroup.category === "Python Projects"
                ? "grid grid-cols-1 sm:grid-cols-1 md:grid-cols-2 gap-6"
                : "grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6"
            }
          >
            {projectGroup.items.map((project) => {
              const isMobileProject =
                projectGroup.category === "Mobile Application Projects";
              const isClickable = isMobileProject; // 仅移动项目可点击

              return (
                <motion.div
                  key={project.title}
                  className={`bg-gray-800 p-6 rounded-2xl shadow-md ${
                    isClickable
                      ? "hover:shadow-lg transform hover:-translate-y-2 transition duration-300 cursor-pointer"
                      : "cursor-default" // 非移动项目默认光标
                  }`}
                  whileHover={isClickable ? { scale: 1.02 } : {}}
                  onClick={isClickable ? () => openModal(project) : undefined} // 仅对移动项目绑定点击事件
                >
                  {/* Project Title */}
                  <h4 className="text-xl font-semibold text-purple-300 mb-2">
                    {project.title}
                  </h4>

                  {/* Tech Stack */}
                  <div className="mb-3 flex flex-wrap gap-2">
                    {project.tech.map((tech) => (
                      <span
                        key={tech}
                        className="bg-purple-600 text-gray-200 text-xs px-2 py-1 rounded-full"
                      >
                        {tech}
                      </span>
                    ))}
                  </div>

                  {/* Description */}
                  <p className="text-gray-400 text-sm mb-4">
                    {project.description}
                  </p>

                  {/* 直接显示图片的项目 */}
                  {isMobileProject && project.images && (
                    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4 mt-4">
                      {project.images.map((image, idx) => (
                        <img
                          key={idx}
                          src={image}
                          alt={`${project.title} Screenshot ${idx + 1}`}
                          className="rounded-lg shadow-md object-cover w-full h-48 sm:h-56 lg:h-[600px]" // 调整高度
                          loading="lazy" // 启用懒加载
                        />
                      ))}
                    </div>
                  )}

                  {/* Enhanced "Live Demo" Button for non-mobile projects */}
                  {!isMobileProject && project.link !== "#" && (
                    <a
                      href={project.link}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="inline-flex items-center bg-gradient-to-r from-indigo-500 to-purple-600 text-white px-4 py-2 rounded-full shadow-md hover:shadow-xl transform hover:scale-105 transition duration-300 mt-4"
                      onClick={(e) => e.stopPropagation()} // Prevent card click
                      aria-label={`View live demo of ${project.title}`} // 提升可访问性
                    >
                      Live Demo
                      <FaExternalLinkAlt className="ml-2" />{" "}
                      {/* 外部链接图标 */}
                    </a>
                  )}
                  {/* 对于 link 为 "#" 的项目不渲染任何按钮 */}
                </motion.div>
              );
            })}
          </div>
        </div>
      ))}

      {/* Modal for Project Details */}
      {selectedProject && (
        <Modal
          isOpen={modalIsOpen}
          onRequestClose={closeModal}
          contentLabel="Project Details"
          className="bg-gray-800 text-gray-100 rounded-3xl p-8 max-w-2xl mx-auto mt-20 relative outline-none"
          overlayClassName="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-start z-50"
        >
          <button
            onClick={closeModal}
            className="absolute top-4 right-4 text-gray-400 hover:text-gray-200 transition"
            aria-label="Close modal"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M6 18L18 6M6 6l12 12"
              />
            </svg>
          </button>
          <h2 className="text-2xl font-bold mb-4">{selectedProject.title}</h2>
          <div className="mb-4">
            {selectedProject.tech.map((tech) => (
              <span
                key={tech}
                className="bg-purple-600 text-gray-200 text-xs px-2 py-1 rounded-full mr-2"
              >
                {tech}
              </span>
            ))}
          </div>
          <p className="mb-4">{selectedProject.details}</p>
          {selectedProject.images && (
            <div className="flex flex-wrap gap-4">
              {selectedProject.images.map((image, idx) => (
                <img
                  key={idx}
                  src={image}
                  alt={`${selectedProject.title} Screenshot ${idx + 1}`}
                  className="rounded-lg shadow-md w-full object-cover h-48 sm:h-56 lg:h-64"
                  loading="lazy" // 启用懒加载
                />
              ))}
            </div>
          )}
          {selectedProject.link !== "#" && (
            <a
              href={selectedProject.link}
              target="_blank"
              rel="noopener noreferrer"
              className="inline-flex items-center mt-4 bg-indigo-500 text-white px-4 py-2 rounded-full hover:bg-indigo-600 transition"
              aria-label={`Visit live demo of ${selectedProject.title}`}
            >
              Visit Project
              <FaExternalLinkAlt className="ml-2" /> {/* 外部链接图标 */}
            </a>
          )}
        </Modal>
      )}
    </section>
  );
};

export default Projects;
